export default {
  name: "goodlist",
  props: {
    goodList: {
      type: Array,
      default: null
    },
    isTitle: {
      type: [Boolean, String],
      default: true
    }
  },
  components: {},
  computed: {
    // 
    goodListFormat() {
      return this.goodList.map(item => {
        return {
          ...item,
        };
      });
    }
  },
  data() {
    return {};
  },
  methods: {
    seeDetail(item) {
      this.$router.push({
        path: "/taobao/taobao_detail",
        query: {
          goods_id:item.item_id, //good_id
          coupon_id:item.coupon_id, //coupon_id
          url:item.coupon_share_url?'https:' + item.coupon_share_url :'https:' +item.url, //url
        }
      });
    }
  }
};



